.AddOrEdit{
  //width: 99%;
  //height: 98%;
  width: 100%;
  height: 100%;
  //margin-top: 10px;
  display: flex;
  flex-direction: column;
  background: white;
  padding-left: 10px;
  overflow: auto;
  &-test{
    display: flex;
    > span{
      display: flex;
    }
  }
  &-title{
    font-size: 16px;
    margin-top: 30px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: #333333;
    line-height: 22px;
  }
  &-info{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    &Content{
      display: flex;
      //align-items: center;
      margin-top: 15px;
    }
  }
  &-input{
    width: 320px;
    height: 42px;
    background: #FFFFFF;
    border-radius: 2px;
    border: 1px solid #D8D8D8;
    margin-left: 20px;
  }
  &-stand{
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #222222;
    line-height: 24px;
    margin-top: 10px;
    margin-left: 5px;
  }
  &-upload{
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 14px;
    margin-left: 10px;
    border: none;
    background: transparent;
  }
  &-divider{
    width: 1px;
    height: 70%;
    color: #e0e0e0;
    margin-top: 30px;
  }
  &-check{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    &Content{
      display: flex;
      width: 99%;
      height: 200px;
      background: #FFFFFF;
      box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.15);
      margin-top: 20px;
    }
  }
  &-table{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-top: 20px;
    &Title{
      display: flex;
      justify-content: space-between;
      &Right{
        display: flex;
        align-items: center;
        cursor: pointer;
        &Btn{
          font-size: 16px;
          font-family: SourceHanSansSC-Normal, SourceHanSansSC;
          font-weight: 400;
          color: #096DD9;
          margin-top: 20px;
          line-height: 24px;
          margin-right: 20px;
        }
      }
    }
  }
  &-add{
    display: flex;
    width: 110px;
    margin-top: 10px;
    cursor: pointer;
  }
  &-footer{
    display: flex;
    margin-left: 10px;
    margin-top: 100px;
  }
}
@primary-color: #1890ff;