.AddOrEditCard{
  width: 100%;
  height: 200px;
  display: flex;
  margin-top: 20px;
  background: white;
  &-card{
    width: 415px;
    height: 100%;
    display: flex;
    &Left{
      width: 134px;
      height: 100%;
      display: flex;
      flex-direction: column;
      &Title{
        margin-left: 40px;
        margin-top: 40px;
        font-size: 16px;
        font-family: SourceHanSansSC-Bold, SourceHanSansSC;
        font-weight: bold;
        color: #333333;
        line-height: 24px;
      }
      &Type{
        margin-top: 10px;
        margin-left: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 84px;
        height: 33px;
        background: #E3DAD0;
        border-radius: 4px;
        &.showBgk{
          background: #EDF9E8;
          color: #4A8C1C;
        }
      }
    }
    &Right{
      width: 280px;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      overflow: auto;
      &Select{
        margin-left: 10px;
        width: 120px;
        height: 30px;
        display: flex;
        border-bottom: 1px solid #f0f0f0;
        align-items: center;
        margin-top: 10px;
        &Title{
          display: inline-block;
          width: 80px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        &.showBgk{
          background: #E0E7F5;
          box-shadow: 0px -1px 0px 0px #E0E7F5;
        }
      }
    }
  }
  ::-webkit-scrollbar{
    width: 6px;
    height: 22px;
    background: white;
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb{
    width: 6px;
    height: 10px;
    background: #e0e0e0;
    border-radius: 4px;
  }
  .ant-checkbox{
    top: -3px;
  }
}
@primary-color: #1890ff;